let num = 60
export default {
    data(){
        return {
            number:'',
            coun : '获取验证码',
            code:'',
            pwd:'',
            repwd:''
            
        }
    },
    methods: {
        register:function(){
            if(this.number.length < 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            if(this.code == '') {
                this.$toast('请输入短信验证码')
                return false
            }
            if(this.pwd.length < 6) {
                this.$toast('请输入大于6位登陆密码')
                return false
            }
            if(this.repwd.length < 6) {
                this.$toast('请输入大于6位确认登陆密码')
                return false
            }
            if(this.pwd != this.repwd ) {
                this.$toast('两次输入的密码不一致')
                return false
            }
            this.$http.getshezhi({
                phone:this.number,
                phonecode:this.code,
                password:this.pwd,
                repwd:this.repwd,
               
            }).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    setTimeout(() => {
                        this.getback()
                    }, 1500);
                }
                
            })
        },
        encode:function(){
            if(this.number.length < 11) {
                this.$toast('请输入正确的手机号')
                return false
            }
            this.$http.getsendcodeWx({phone: this.number}).then(res=>{
                if(res.status == 200) {
                    this.$toast(res.mess)
                    var timeId = setInterval( res=>{
                        --num
                        if(num == 0) {
                            this.coun = '重新获取'
                            clearInterval(timeId)
                            num = 60
                        return false
                        }
                        this.coun = num + 's'
                       
                    } ,1000)
                }
            })
        }
    },
}
